import { CommonModule } from '@angular/common';
import { Component, Injector, ViewChild, ViewContainerRef } from '@angular/core';

@Component({
  selector: 'lib-modal-container',
  standalone: true,
  imports: [
    CommonModule
  ],
  template: `
      <div [ngClass]="modalActivo ? 'modal is-active' : 'modal'">
      <div class="modal-background" (click)="closeModal()"></div>
      <div class="modal-content">
        <div class="box">
          <ng-template #modalContainer></ng-template>
        </div>
      </div>
    </div>
  `,
  styles: `
    .modal-content {
      width: 90%;
      height: 85%;
      overflow: hidden;
    }

    .box {
      height: 100%;
    }
  `
})
export class ModalContainerComponent {

  public modalActivo: boolean = false;

  @ViewChild('modalContainer', { read: ViewContainerRef }) public modalContainer!: ViewContainerRef;

  constructor(
    private injector: Injector
  ) { }

  public openModal(componentType: any, inputs: any = {}, outputs: any = {}) {
    if (this.modalActivo) {
      return;
    }

    this.modalActivo = true;
    const componentRef = this.modalContainer.createComponent(componentType, {
      injector: this.injector
    }) as any;

    // Establecer inputs
    for (const input in inputs) {
      if (inputs.hasOwnProperty(input)) {
        componentRef.instance[input] = inputs[input];
      }
    }

    // Manejar outputs (eventos)
    for (const output in outputs) {
      if (outputs.hasOwnProperty(output)) {
        componentRef.instance[output].subscribe(outputs[output]);
      }
    }
  }

  public closeModal() {
    this.modalActivo = false;
    this.modalContainer.clear();
  }

}
